  flex-basis: 0; }

.flex-auto {
  flex: auto; }

.flex-col {
  display: flex !important;
  flex-direction: column;
  min-width: 0; }

.flex-center {
  display: flex !important;
  align-items: center;
  justify-content: center;
  min-width: 0; }

.flex-fill-row {
  flex: 0 1 100%; }

.flex-row {
  display: flex;
  flex-direction: row;
  min-width: 0; }

.flex-row-wrap {
